<template>
    <div class="one">
        <div class="one-top" @click="clicknews">
            <news></news>
        </div>
        <div class="wrapper" 
             @touchmove="phonetouchend"  
             @touchstart="phonetouchstart"
        >
            <div class="wrapper-one" >
                <span class="main-word">改善空气质量</span>
                <div class="include-arrow" @click="pictureArrowClick">
                    <span class="main-word">提高呼吸安全的产品</span>
                    <arrow></arrow>
                </div>
            </div>
            <div class="wrapper-two" ref="listion">
                <logo></logo>
                <span class="company-name">上海蕴贝实业有限公司</span>
                <img src="../../../static/one-img/cloud.jpg" class="bgimg"/>
                <div class="include-arrow">
                    <span class="induct">关于我们</span>
                    <arrow></arrow>
                </div>
                <p v-for="item in mainWord" class="mainword">{{item}}</p>
                <img src="../../../static/one-img/hand.jpg" class="bgimg hand"/>
                <div class="include-arrow-2" @click="cooperateArrowClick">
                    <span class="induct">我们的合作伙伴</span>
                    <arrow></arrow>
                </div>
                <img src="../../../static/one-img/car.jpg" class="bgimg car"/>
                <div class="include-arrow-2"  @click="customerArrowClick">
                    <span class="induct">我们的客户</span>
                    <arrow></arrow>
                </div>
                <img src="../../../static/one-img/test.jpg" class="bgimg test"/>
                <div class="include-arrow-2">
                    <span class="induct"  @click="machineArrowClick">我们的检测仪器</span>
                    <arrow></arrow>
                </div>
                <div class="round" @click="roundClick">
                    <div v-for="item in arr" class="inner-round"></div>
                </div>
            </div>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
import news from 'common/svg/news'
import arrow from 'common/svg/arrow'
import logo from 'common/svg/logo'

export default {
    props: {

    },
    data() {
        return {
            mainWord: ["上海蕴贝实业有限公司成立于2011年，公司致力于改善空气质量、提高呼吸安全的产品生产，产品广泛用于医疗器械、吸尘器、空气清新机、中央空调及吸尘系统、工业生产等领域。","公司从成立开始即将产品定位于中高端市场，与3M、HV、Donaldson 等著名公司战略合作，采用国际先进材料，共同开发、生产高质量的产品，产品出口北美、欧洲等市场 。","公司一贯坚持质量第一的原则，通过莱茵TUV ISO9001：2008质量管理体系认证，并始终严格执行体系要求，不断强化内部管理，确保产品质量稳定、可靠。","对于员工，公司始终提倡和谐、轻松的工作环境，鼓励员工主动、创新，为每个员工提供良好的职业规划和社会保障。"],
            arr: [1,2,3],
            newsflag: true
        };
    },
    computed: {

    },
    created() {
        this.touch = {}
    },
    mounted() {
        console.log(pageYOffset)
    },
    watch: {

    },
    methods: {
        clicknews() {
            if(this.newsflag) {
                this.$router.push('news')
                this.newsflag = !this.newsflag
            } else {
                this.$router.go(-1)
                this.newsflag = !this.newsflag
            }
        },
        pictureArrowClick() {
            this.$router.push('pictureList')
        },
        cooperateArrowClick() {
            this.$router.push('cooperate')
        },
        customerArrowClick() {
            this.$router.push('customer')
        },
        machineArrowClick() {
            this.$router.push('machine')
        },
        roundClick() {
            this.$router.push('people')
        },
        handleScroll(e) {
        },
        phonetouchstart(e) {
            //  this.touch.initiated = true
            // 用来判断是否是一次移动
            // this.touch.moved = false
            const touch = e.touches[0]
            this.touch.startX = touch.pageX
            this.touch.startY = touch.pageY
        },
        phonetouchend(e) {
            // if (!this.touch.initiated) {
            // return
            // }
            const touch = e.touches[0]
            const deltaX = touch.pageX - this.touch.startX
            let deltaY = touch.pageY - this.touch.startY
            // if (Math.abs(deltaY) > Math.abs(deltaX)) {
            // return
            // }
            // if (!this.touch.moved) {
            // this.touch.moved = true
            // }
            console.log(innerHeight)
        }
    },
    components: {
        arrow,
        news,
        logo
    },
};
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
    @import "~common/styles/variable"
	@import "~common/styles/mixin"
    
    .one
        // position: relative
        overflow-y: scroll
        height: 100vh
        .one-top
            // position: absolute
            float: right
            top: 0
            right: 0
        .wrapper
            padding-top: 100px
            .wrapper-one
                margin: 100px 0 0 40px
                .main-word
                    font-size: $f-main-sub
                    color: $c-bg-heavy
                    line-height: 3rem
            .wrapper-two
                margin-left: 40px
                height: auto
                .logo
                    display: block
                    padding-top: 40px
                .company-name
                    font-size: $f-text
                    color: $c-bg-heavy
                    line-height: 2rem
                    margin-bottom: 5px
                .bgimg
                    // height: 600px
                    width: calc(100% - 40px)
                .include-arrow
                    margin-top: 10px
                    display: flex
                    align-items: center
                    .main-word
                        font-size: $f-main-sub
                        color: $c-bg-heavy
                        line-height: 3rem
                    .induct
                        font-size: $f-text
                        color: $c-bg-heavy
                        line-height: 2rem
                        margin-bottom: 5px
                    .arrow
                        font-size: $f-text
                        color: $c-bg-heavy
                        line-height: 2rem
                        margin-left: 5px
                        transform: rotate(90deg)
                .mainword
                    font-size: $f-link
                    color: $c-bg-heavy
                    width: calc(100% - 35px)
                    line-height: 1.2rem
                    margin-top: 8px
                .hand
                    margin-top: 70px
                .include-arrow-2
                    margin-top: 10px
                    display: flex
                    align-items: center
                    .induct
                        font-size: $f-text
                        color: $c-bg-heavy
                        line-height: 2rem
                    .arrow
                        margin-left: 10px
                        line-height: 2rem
                .car
                    margin-top: 80px
                .test
                    margin-top: 40px
                .round
                    width: 100px
                    height: 100px
                    border-radius: 50%
                    border: 1px solid $c-bg-heavy
                    margin: 60px 0 60px
                    margin-left: calc(50% - 70px)
                    display: flex
                    align-items: center
                    justify-content: center
                    .inner-round
                        line-height: 100px
                        width: 3px
                        height: 3px
                        border-radius: 50%
                        border: 1px solid $c-bg-heavy
                        text-align: center
                        margin: 3px
</style>



